import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../../../utils/axiosInstance';  // Axios  
import './ProductAdmin.css';
import {toast} from "react-toastify";

function CreateProduct() {
    const [productName, setProductName] = useState('');
    const [productDescription, setProductDescription] = useState('');
    const [category, setCategory] = useState('');
    const [categories, setCategories] = useState([]);
    const [options, setOptions] = useState([{ name: '', price: '', quantity: '', imageUrl: '' }]);
    const [productImages, setProductImages] = useState([{ imageUrl: '' }]); //   
    const navigate = useNavigate();
    const BASE_URL = "http://43.203.127.251:8080";
    const token = localStorage.getItem('access'); //  JWT  

    //      
    const handleOptionChange = (index, field, value) => {
        const newOptions = [...options];
        newOptions[index][field] = value;
        setOptions(newOptions);
    };

    //      
    const handleImageChange = (index, value) => {
        const newImages = [...productImages];
        newImages[index].imageUrl = value;
        setProductImages(newImages);
    };

    //  
    const addOption = () => {
        setOptions([...options, { name: '', price: '', quantity: '', imageUrl: '' }]);
    };

    //  
    const addImage = () => {
        setProductImages([...productImages, { imageUrl: '' }]);
    };

    //  
    const removeOption = (index) => {
        const newOptions = options.filter((_, idx) => idx !== index);
        setOptions(newOptions);
    };

    //  
    const removeImage = (index) => {
        const newImages = productImages.filter((_, idx) => idx !== index);
        setProductImages(newImages);
    };

    //       (Axios )
    useEffect(() => {
        const fetchCategories = async () => {
            try {
                const response = await fetch(BASE_URL+'/api/admin/category', {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Authorization': `Bearer ${token}`
                    },
                });

                if (!response.ok) {
                    throw new Error('Failed to fetch category data');
                }

                const data = await response.json(); // JSON    
                setCategories(data); //    
                console.log("Fetched categories:", data);
            } catch (error) {
                console.error('  :', error);
            }
        };

        fetchCategories();
    }, []);

    //   
    const handleSubmit = async (event) => {
        event.preventDefault();

        //    
        const productData = {
            name: productName,
            description: productDescription,
            categoryId: category,
            productDetails: options.length > 0 ? options.map(option => ({
                name: option.name,
                price: option.price,
                quantity: option.quantity,
                mainImage: option.imageUrl
            })) : [], //     
            productImages: productImages.length > 0 ? productImages.map(image => ({
                imageUrl: image.imageUrl
            })) : [] //     
        };

        try {
            //   
            const response = await fetch(BASE_URL+'/api/admin/products', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': `Bearer ${token}`
                },
                body: JSON.stringify(productData)  //  JSON  
            });

            if (!response.ok) {
                throw new Error('    .');
            }

            const responseData = await response.json();
            toast.success('  .', {
                position: "top-center",
                autoClose: 2000,
            });
            navigate('/admin/product');
        } catch (error) {
            console.error('    :', error);
            toast.error('    .', {
                position: "top-center",
                autoClose: 2000,
            });
        }
    };

    return (
        <div className="container">
            <h2>  </h2>
            <form onSubmit={handleSubmit}>
                <div className="form-group">
                    <label> :</label>
                    <input
                        type="text"
                        className="form-control"
                        value={productName}
                        onChange={(e) => setProductName(e.target.value)}
                        required
                    />
                </div>

                <div className="form-group">
                    <label> :</label>
                    <textarea
                        className="form-control"
                        value={productDescription}
                        onChange={(e) => setProductDescription(e.target.value)}
                        required
                    />
                </div>

                <div className="form-group">
                    <label>:</label>
                    <select
                        className="form-control"
                        value={category}
                        onChange={(e) => setCategory(e.target.value)}
                        required
                    >
                        <option value=""> </option>
                        {categories.map((cat) => (
                            <option key={cat.id} value={cat.id}>{cat.name}</option>
                        ))}
                    </select>
                </div>
                <hr></hr>
                <h3> </h3>
                {options.map((option, index) => (
                    <div key={index} className="form-group">
                        <label> {index + 1}</label>
                        <input
                            type="text"
                            placeholder=" "
                            value={option.name}
                            onChange={(e) => handleOptionChange(index, 'name', e.target.value)}
                            required
                            className="form-control"
                        />
                        <input
                            type="number"
                            placeholder=" "
                            value={option.price}
                            onChange={(e) => handleOptionChange(index, 'price', e.target.value)}
                            required
                            className="form-control"
                        />
                        <input
                            type="number"
                            placeholder=" "
                            value={option.quantity}
                            onChange={(e) => handleOptionChange(index, 'quantity', e.target.value)}
                            required
                            className="form-control"
                        />
                        <input
                            type="text"
                            placeholder=" URL"
                            value={option.imageUrl}
                            onChange={(e) => handleOptionChange(index, 'imageUrl', e.target.value)}
                            className="form-control"
                        />
                        <button type="button" onClick={() => removeOption(index)} className="btn btn-secondary"> 
                        </button>
                    </div>
                ))}
                <hr></hr>
                <h3> </h3>
                {productImages.map((image, index) => (
                    <div key={index} className="form-group">
                        <label> {index + 1}</label>
                        <input
                            type="text"
                            placeholder=" URL"
                            value={image.imageUrl}
                            onChange={(e) => handleImageChange(index, e.target.value)}
                            required
                            className="form-control"
                        />
                        <button type="button" onClick={() => removeImage(index)} className="btn btn-secondary"> </button>



                    </div>
                ))}


                <div>
                    <button type="button" onClick={addOption} className="btn btn-secondary"> </button>
                    <button type="button" onClick={addImage} className="btn btn-secondary"> </button>
                    <button type="submit" className="btn btn-secondary"> </button>
                </div>
            </form>
        </div>
    );
}

export default CreateProduct;
