import React, { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axiosInstance from '../../../utils/axiosInstance';
import {toast} from "react-toastify"; // Axios  

function ProductDetailCreate() {
    const { productId } = useParams(); // URL productId 
    const navigate = useNavigate(); //     
    const [option, setOption] = useState({
        name: '',
        price: 0,
        quantity: 0,
        mainImage: ''
    });
    const BASE_URL = "http://43.203.127.251:8080";

    //   
    const handleChange = (e) => {
        const { name, value } = e.target;
        setOption({
            ...option,
            [name]: value
        });
    };

    //    ( )
    const handleSubmit = async (e) => {
        e.preventDefault();

        const token = localStorage.getItem('access'); //  JWT  

        try {
            const response = await fetch(BASE_URL+`/api/admin/products/${productId}/details`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`, // Authorization  
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(option) //    JSON  
            });

            if (!response.ok) {
                throw new Error('    .');
            }

            const responseData = await response.json();
            toast.success('  .', {
                position: "top-center",
                autoClose: 2000,
            });
            navigate(`/admin/edit-product/${productId}`); //    

        } catch (error) {
            console.error('    :', error);
            toast.error('    .', {
                position: "top-center",
                autoClose: 2000,
            });
        }
    };

    return (
        <div className="container">
            <h2> </h2>
            <form onSubmit={handleSubmit}>
                <div className="form-group">
                    <label> :</label>
                    <input
                        type="text"
                        className="form-control"
                        name="name"
                        value={option.name}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label> :</label>
                    <input
                        type="number"
                        className="form-control"
                        name="price"
                        value={option.price}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label> :</label>
                    <input
                        type="number"
                        className="form-control"
                        name="quantity"
                        value={option.quantity}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="form-group">
                    <label> :</label>
                    <input
                        type="text"
                        className="form-control"
                        name="mainImage"
                        value={option.mainImage}
                        onChange={handleChange}
                    />
                </div>
                <div className="text-end"> {/*       */}
                    <button type="submit" className="btn btn-secondary"> </button> {/*   */}
                </div>
            </form>
        </div>
    );
}

export default ProductDetailCreate;

