import React from 'react';
import OrderItem from './OrderItem';

function OrderList({ orders, onDelete }) {
    return (
        <div>
            <div className="admin-order-header">
                <span style={{marginLeft: '10px', width: '200px'}}><strong>ID</strong></span>
                <span style={{marginLeft: '25px', width: '400px'}}><strong></strong></span>
                <span style={{marginLeft: '23px'}}><strong></strong></span>
                <span style={{marginLeft: '12px',width: '500px'}}><strong></strong></span>
                <span style={{marginLeft: '65px', width: '500px'}}><strong></strong></span>
                <span style={{marginLeft: '14px'}}><strong></strong></span>
                <span style={{marginLeft: '3px'}}><strong> </strong></span>
                <span style={{}}><strong></strong></span>
            </div>
            <ul className="admin-order-list">
                {orders.map((order) => (
                    <OrderItem
                        key={order.id}
                        order={order}
                        onDelete={onDelete}
                    />
                ))}
            </ul>
        </div>
    );
}

export default OrderList;
