import { jwtDecode } from "jwt-decode";

/* Refresh Token */


/* Access Token */

//    
const isAccessTokenExist = (token) => {
    if (!token) {
        console.log('  .');
        return false;
    }

    if (typeof token !== 'string') {
        console.log('  .');
        return false;
    }

    console.log(':  .');
    return true;
}

//    
export const isAccessTokenExpired = (token) => {
    try {
        const decodedToken = jwtDecode(token);
        const currentTime = Date.now() / 1000;

        return decodedToken.exp < currentTime;
    } catch (error) {
        console.error('Token validation error: ', error.message);
        return false;
    }
}


//   
const isAccessTokenValid = (token) => {

    // 1.    .
    if (!isAccessTokenExist(token)) {
        console.log('  .');
        return false;
    }

    // 2.    .
    if (isAccessTokenExpired(token)) {
        console.log(' .');
        return false;
    }

    console.log('   ');
    return true;
}


//  
const reissueToken = async () => {
    try {
        const response = await fetch('http://43.203.127.251:8080/api/auth/reissue', {
            method: 'POST',
            credentials: 'include' //   
        });

        if (!response.ok) {
            throw new Error('  ');
        }

        const newAccessToken = response.headers.get('Authorization').split(' ')[1];
        localStorage.setItem('access', newAccessToken); //    

        console.log('  .');
        return newAccessToken;
    } catch (error) {
        console.error('  : ', error);
        return null;
    }
};


//    
const checkAndReissueToken = async () => {
    const token = localStorage.getItem('access');

    //   
    if (!isAccessTokenValid(token)) {
        console.log('    .');
        const newToken = await reissueToken();
        return newToken ? true : false;
    }

    console.log(' .');
    return true;
};

export { isAccessTokenValid, reissueToken, checkAndReissueToken };