import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './User.css';

function SignUpResult() {
    const location = useLocation();
    const { name, email } = location.state || {};

    const navigate = useNavigate();

    return (
        <div className="user-container h-100 d-flex justify-content-center vh-100 align-items-center">
            <div className="col-md-6 user-form">
                {name ? (
                    <h3 className="found-email">  , {name} ! </h3>
                ) : (
                    <h3> .</h3>
                )}
                <hr />
                {email ? (
                    <p className="found-email">  {email} .</p>
                ) : (
                    <p>   .</p>
                )}
                <div className="d-flex justify-content-end mt-3">
                    <button className="btn btn-sm user-btn btn-dark" onClick={() => navigate('/login')}>
                         
                    </button>
                </div>
            </div>
        </div>
    );
}

export default SignUpResult;
