import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../../utils/axiosInstance';
import { getUserEmail, isLoggedIn } from '../../utils/authUtils';
import { ToastContainer, toast } from 'react-toastify';
import './User.css';
import './MyPage.css'

function MyPage() {
    const [user, setUser] = useState(null);
    const [errorMessage, setErrorMessage] = useState('');
    const [loading, setLoading] = useState(true);
    const [activeTab, setActiveTab] = useState(' ');
    const [purchaseHistory, setPurchaseHistory] = useState([]);
    const [gradeInfo, setGradeInfo] = useState(null);
    const [canceledOrders, setCanceledOrders] = useState([]);
    const navigate = useNavigate();

    const fetchUser = async (email) => {
        try {
            const token = localStorage.getItem('access');

            const response = await fetch(`http://43.203.127.251:8080/api/user/mypage?email=${email}`, {
                method: 'GET',
                headers: {
                    'Authorization' : `Bearer ${token}`,
                    'Content-Type' : 'application/json'
                }
            });

            if (!response.ok) {
                throw new Error('    .');
            }
            const data = await response.json();
            setUser(data);

        } catch (error) {
            if (error.response?.status === 401) {
                setErrorMessage(' .');
                navigate('/login');
            } else {
                setErrorMessage('    .');
            }
        } finally {
            setLoading(false);
        }
    };

    //    
    const fetchPurchaseHistory = async (email) => {
        try {
            setPurchaseHistory([]);
            const response = await fetch(`http://43.203.127.251:8080/api/orders?email=${email}`);
            if (!response.ok) {
                throw new Error('    .');
            }
            const data = await response.json();
            setPurchaseHistory(Array.isArray(data) ? data : []);
        } catch (error) {
            console.error('', error);
            setPurchaseHistory([]);
        }
    };

    //   
    const handleCancelOrder = (orderId) => {
        toast.info(
            <div>
                <span style={{ marginBottom: '10px' }}>
                    <i className="fas fa-info-circle"></i>
                </span>
                <p style={{ margin: 0 }}>   ?</p>
                <div style={{ display: 'flex', marginLeft: '75px', fontSize: '0.9rem' }}>
                    <button
                        onClick={() => handleConfirmCancel(orderId)}
                        style={{ color: 'green' }}
                    >
                        
                    </button>
                    <button
                        onClick={() => toast.dismiss()}
                        style={{ color: 'red' }}
                    >
                        
                    </button>
                </div>
            </div>,
            {
                position: "top-center",
                autoClose: 3000,
                closeOnClick: false,
                closeButton: false,
            }
        );
    };

    //   , 
    const handleConfirmCancel = async (orderId) => {
        try {
            toast.dismiss();
            const response = await fetch(`http://43.203.127.251:8080/api/orders/${orderId}`, { method: 'PATCH' });
            if (!response.ok) {
                throw new Error('  .');
            }
            toast.success(" .", {
                position: "top-center",
                autoClose: 2000,
            });

            setCanceledOrders([...canceledOrders, orderId]);

            const email = getUserEmail();
            fetchPurchaseHistory(email);
        } catch (error) {
            toast.error("  !", {
                position: "top-center",
                autoClose: 2000,
            });
        }
    };

    //    
    const fetchUserGrade = async (email) => {
        try {
            const response = await fetch(`http://43.203.127.251:8080/api/user/${email}`);
            if (!response.ok) {
                throw new Error('    .');
            }
            const data = await response.json();
            setGradeInfo(data);
        } catch (error) {
            setErrorMessage(error.message);
        }
    };
    
    //     
    const fetchDataByTab = (email) => {
        if (activeTab === ' ') {
            fetchPurchaseHistory(email);
        } else if (activeTab === ' ') {
            fetchUserGrade(email);
        }
    };

    useEffect(() => {
        if (!isLoggedIn()) {
            setErrorMessage(' .');
            navigate('/login');
            return;
        }
        const email = getUserEmail();
        if (email) {
            fetchUser(email);
            fetchDataByTab(email);
        } else {
            setErrorMessage('    .');
            setLoading(false);
        }
    }, [navigate, activeTab]);

    const handleEdit = async (field) => {
        const email = getUserEmail();
        let updatedValue;
        let endpoint;
        switch (field) {
            case 'address':
                updatedValue = user.address;
                endpoint = `http://43.203.127.251:8080/api/user/address/${email}`;
                break;
            case 'password':
                updatedValue = prompt('  :');
                endpoint = `http://43.203.127.251:8080/api/user/password/${email}`;
                break;
            default:
                return;
        }

        try {
            const token = localStorage.getItem('access'); // Access token 
            const response = await fetch(endpoint, {
                method: 'PUT',
                headers: {
                    'Authorization': `Bearer ${token}`, //   
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    [field]: updatedValue,
                }),
            });

            if (response.ok) {
                alert(`${field}()  .`);
                fetchUser(email); //     
            } else {
                const errorData = await response.json();
                alert(`   : ${errorData.message || 'Unknown error'}`);
            }
        } catch (error) {
            console.error('    :', error);
            alert('  .');
        }
    };


    //    
    const formatPriceWithWon = (price) => {
        return new Intl.NumberFormat('ko-KR').format(price) + '';
    };

    //   
    const renderPurchaseHistory = () => (
        <div className="custom-table-container">
            <table className="custom-table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th> </th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {purchaseHistory && purchaseHistory.length > 0 ? (
                        purchaseHistory.map((order) => (
                            <React.Fragment key={order.id}>
                                {order.orderDetails.map((item, index) => (
                                    <tr key={`${order.id}-${item.productDetailId}-${index}`}>
                                        {index === 0 && (
                                            <>
                                                <td>
                                                    <img
                                                        src={item.productImage}
                                                        alt={item.productName}
                                                        style={{ width: '80px', height: '80px' }}
                                                    />
                                                </td>
                                                <td style={{ width: '130px' }}>{item.productName}</td>
                                                <td style={{ width: '50px' }}>{item.quantity}</td>
                                                <td style={{ width: '110px' }}>{formatPriceWithWon(item.subTotalPrice)}</td>
                                                <>
                                                    <td rowSpan={order.orderDetails.length} style={{ width: '110px' }}>
                                                        {formatPriceWithWon(order.totalPrice)}
                                                    </td>
                                                    <td rowSpan={order.orderDetails.length} style={{ width: '50px' }}>
                                                        {order.deliveryStatus}
                                                    </td>
                                                    <td rowSpan={order.orderDetails.length} style={{ width: '100px' }}>
                                                        {order.createdAt}
                                                    </td>
                                                    <td rowSpan={order.orderDetails.length}>
                                                        {order.orderStatus === 'CANCEL' ? (
                                                            <button
                                                                className="btn btn-cancel-success"
                                                                disabled
                                                            >
                                                                 
                                                            </button>
                                                        ) : (
                                                            <button
                                                                className="btn btn-danger"
                                                                onClick={() => handleCancelOrder(order.id)}
                                                                style={{ fontSize: '0.6rem' }}
                                                            >
                                                                 
                                                            </button>
                                                        )}
                                                    </td>
                                                </>
                                            </>
                                        )}
                                        {index > 0 && (
                                            <>
                                                <td>
                                                    <img
                                                        src={item.productImage}
                                                        alt={item.productName}
                                                        style={{ width: '80px', height: '80px' }}
                                                    />
                                                </td>
                                                <td>{item.productName}</td>
                                                <td>{item.quantity}</td>
                                                <td>{formatPriceWithWon(item.subTotalPrice)}</td>
                                            </>
                                        )}
                                    </tr>
                                ))}
                            </React.Fragment>
                        ))
                    ) : (
                        <tr>
                            <td colSpan="9" style={{ textAlign: 'center' }}>  .</td>
                        </tr>
                    )}
                </tbody>
            </table>
            <ToastContainer position="top-right" autoClose={3000} hideProgressBar={false} closeOnClick pauseOnHover />
        </div>
    );

    //   
    const renderUserGrade = () => (
        <div style={{ fontWeight: 'bold' }}>
            {user && gradeInfo ? (
                <p>
                    {user.name}  
                    <span style={{ color: 'lightgreen', textDecoration: 'underline' }}> {gradeInfo.grade} </span>
                    .
                    <div className="grade-benefits">
                        <h6 style={{ fontSize: '0.9rem', textDecoration: 'underline' }}>  </h6>
                        <p>     .</p>
                        <ul>
                            <li>
                                <strong> GOLD </strong> <br />
                                <span>   <strong>3,000,000</strong>   GOLD  .</span><br />
                                <span>GOLD   <strong> </strong>   <strong>  10% </strong>   .</span><br />
                            </li>
                            <li style={{ marginTop: '20px' }}>
                                <strong> SILVER </strong> <br />
                                <span>   <strong>2,000,000</strong>   SILVER  .</span><br />
                                <span>SILVER   <strong>  5% </strong>    .</span>
                            </li>
                        </ul>
                        <div className="grade-note">
                            <p><em>      .</em></p>
                        </div>
                    </div>
                </p>
            ) : (
                <p>   ...</p>
            )}
        </div>
    );

    const renderContent = () => {
        switch (activeTab) {
            case ' ':
                return (
                    <div>
                        <div className="form-group mb-3">
                            <label htmlFor="email"></label>
                            <div className="input-group">
                                <input type="email" className="form-control user-form-control" id="email" value={user.email} disabled />
                            </div>
                        </div>
                        <div className="form-group mb-3">
                            <label htmlFor="name"></label>
                            <div className="input-group">
                                <input type="text" className="form-control user-form-control" id="name" value={user.name} disabled />
                            </div>
                        </div>
                        <div className="form-group mb-3">
                            <label htmlFor="phoneNumber"></label>
                            <div className="input-group">
                                <input type="tel" className="form-control user-form-control" id="phoneNumber" value={user.phoneNumber} disabled />
                            </div>
                        </div>
                        <div className="form-group mb-3">
                            <label htmlFor="address"></label>
                            <div className="input-group">
                                <input type="text" className="form-control user-form-control" id="address" value={user.address} onChange={(e) => setUser({ ...user, address: e.target.value })} />
                                <div className="input-group-append">
                                    <button className="btn btn-sm user-btn" onClick={() => handleEdit('address')}></button>
                                </div>
                            </div>
                        </div>
                        <div className="form-group mb-3">
                            <label htmlFor="password"></label>
                            <div className="input-group">
                                <input type="password" className="form-control user-form-control" id="password" value="" disabled />
                                <div className="input-group-append">
                                    <button className="btn btn-sm user-btn" onClick={() => handleEdit('password')}></button>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            case ' ':
                return renderPurchaseHistory();
            case ' ':
                return renderUserGrade();
            default:
                return null;
        }
    };

    if (loading) return <p>   ...</p>;
    if (errorMessage) return <p className="text-danger">{errorMessage}</p>;

    return (
        <div className="user-mypage-container">
            <div className="user-mypage-content">
                <div className="user-header">
                    <h3 className="text-left mb-2"></h3>
                </div>
                <div className="user-body">
                    <hr />
                    <div className="tab-container user-tab-container">
                        {[' ', ' ', ' '].map((tab, index) => (
                            <React.Fragment key={tab}>
                                <button
                                    className={`btn ${activeTab === tab ? 'btn-dark' : 'btn-light'}`}
                                    onClick={() => setActiveTab(tab)}
                                >
                                    {tab}
                                </button>
                                {index < 2 && <div className="tab-divider"></div>}
                            </React.Fragment>
                        ))}
                    </div>
                    <hr />
                    <div className="user-content-container">
                        {loading ? (
                            <p>   ...</p>
                        ) : errorMessage ? (
                            <p className="text-danger">{errorMessage}</p>
                        ) : (
                            renderContent()
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default MyPage;