import React, {useEffect, useState} from 'react';
import {Link, useLocation, useNavigate} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './User.css';
import {syncWithLocal} from "../../utils/cartUtils";
import {useAuth} from "../../context/AuthContext";
import logo from "../../logo.svg";

function Login() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const navigate = useNavigate();
    const location = useLocation();
    const { handleContextLogin } = useAuth();

    const validateEmail = (email) => {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    };

    const handleLogin = async (event) => {
        event.preventDefault();

        if (!email && !password) {
            setErrorMessage(",  .");
            return;
        } else if (!email) {
            setErrorMessage("  .");
            return;
        } else if (!validateEmail(email)) {
            setErrorMessage("   .");
            return;
        } else if (!password) {
            setErrorMessage("  .");
            return;
        }

        const response = await fetch('http://43.203.127.251:8080/api/auth/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ email, password }),
            credentials: 'include' //    
        });

        if (response.ok) {
            //   access  
            const accessToken = response.headers.get('Authorization');
            if (accessToken && accessToken.startsWith('Bearer ')) {
                localStorage.setItem('access', accessToken.split(' ')[1]); // access    

                //     Cart    
                handleContextLogin(); //   
                syncWithLocal();
                localStorage.removeItem('cart');


                //    
                // , restrictedPages  URL   
                const restrictedPages = ['/signup/result'];
                const fromPage = location.state?.from || '/';  //   
                if (!restrictedPages.includes(fromPage)) {
                    navigate(fromPage);
                } else {
                    navigate("/");  //      
                }


            } else {
                setErrorMessage('Access   .');
            }
        } else {
            if (response.status === 401) {
                setErrorMessage('   .');
            } else if (response.status === 403) {
                setErrorMessage('  .');
            } else {
                setErrorMessage(' .     .');
            }
        }
    };

    const handleBack = () => {
        navigate("/");
    };

    //   
    useEffect(() => {
        const handleAuthChange = () => {
        };
        window.addEventListener('authChange', handleAuthChange);
        return () => {
            window.removeEventListener('authChange', handleAuthChange);
        };
    }, []);

    return (
        <div className="login-container d-flex flex-column align-items-center justify-content-start vh-100 card border-0">
            <div className="user-login-container">
                <div className="user-login-logo text-center mb-5">
                    <Link to="/"><img src={logo} alt="logo" width="180"/></Link>
                </div>
                <div className="text-center">
                    <h5 className="user-login-text mb-3 fw-semibold">  ''</h5>
                </div>
                <form onSubmit={handleLogin} className="d-flex flex-column">
                    <input
                        type="email"
                        className="form-control user-form-control mb-3"
                        placeholder=""
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                    />
                    <input
                        type="password"
                        className="form-control user-form-control mb-4"
                        placeholder=""
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                    {errorMessage && <p className="text-danger mb-3">{errorMessage}</p>}
                    <button type="submit" className="btn user-login-btn mb-4"></button>
                </form>
                <div className="d-flex justify-content-between mb-5">
                    <Link to="/find" className="user-link"> </Link>
                    <Link to="/reset" className="user-link"> </Link>
                    <Link to="/signup" className="user-link"></Link>
                </div>
                <hr className="mb-3"/>
                <div className="text-center">
                    <p className="bottom-text mb-0"> Elice Cloud Track 4 2  5</p>
                </div>

            </div>
        </div>
    );
}

export default Login;
