import React, { useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './User.css';  //  FindAccount  CSS 
import logo from "../../logo.svg";

function ResetPassword() {
    const location = useLocation();
    const navigate = useNavigate();

    const [email, setEmail] = useState(location.state?.email || '');
    const [name, setName] = useState(location.state?.name || '');
    const [errorMessage, setErrorMessage] = useState('');
    const [successMessage, setSuccessMessage] = useState('');

    const validateEmail = (email) => {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    };

    const handleResetPassword = async (event) => {
        event.preventDefault();

        //       
        if (!email && !name) {
            setErrorMessage("  .");
            return;
        } else if (!email) {
            setErrorMessage("  .");
            return;
        } else if (!validateEmail(email)) {
            setErrorMessage("   .");
            return;
        } else if (!name) {
            setErrorMessage("  .");
            return;
        }

        try {
            const response = await fetch('http://43.203.127.251:8080/api/auth/check', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ name, email }),
            });

            const checkResult = await response.json();

            if (checkResult.data === true) {
                //      
                navigate('/reset/result', { state: { email } });

                //     API 
                await fetch('http://43.203.127.251:8080/api/auth/check/reset', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({ email, name }),
                });
            } else {
                setErrorMessage('    .');
                setSuccessMessage('');
            }
        } catch (error) {
            setErrorMessage(error.message || '    .');
            setSuccessMessage('');
        }
    };

    return (
        <div className="user-find-container h-100 d-flex justify-content-center align-items-center card border-0">
            <div className="col-md-6">
                <div className="user-login-logo text-center mb-5">
                    <Link to="/"><img src={logo} alt="logo" width="140" /></Link>
                </div>
                <h3 className="text-left mb-2"> </h3>
                <hr />
                <form onSubmit={handleResetPassword}>
                    <div className="form-group mb-3">
                        <label htmlFor="name"></label>
                        <input
                            type="text"
                            className="form-control user-form-control"
                            id="name"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            required
                        />
                    </div>
                    <div className="form-group mb-3">
                        <label htmlFor="email"></label>
                        <input
                            type="email"
                            className="form-control user-form-control"
                            id="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            required
                            style={{ marginBottom: '40px' }}
                        />
                    </div>
                    {/*        */}
                    {errorMessage && <p className="text-danger">{errorMessage}</p>}
                    {successMessage && <p className="text-success">{successMessage}</p>}
                    <button type="submit" className="btn btn-sm user-btn btn-dark"> </button>
                </form>
            </div>
        </div>
    );
}

export default ResetPassword;
