import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../../../utils/axiosInstance';
import {toast} from "react-toastify"; // Axios  

function ProductList({ products, setProducts }) {
    const [categories, setCategories] = useState([]); //   
    const navigate = useNavigate();
    const BASE_URL = "http://43.203.127.251:8080";
    const token = localStorage.getItem('access'); //  JWT  

    //    
    useEffect(() => {
        const fetchCategories = async () => {
            try {
                const response = await fetch(BASE_URL+'/api/admin/category', {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Authorization': `Bearer ${token}`, // Authorization  
                    },
                });

                if (!response.ok) {
                    throw new Error('  ');
                }

                const data = await response.json(); // JSON  
                setCategories(data); //    
                console.log("Fetched categories:", data);
            } catch (error) {
                console.error('  :', error);
            }
        };

        fetchCategories();
    }, []);

    //   
    const handleDelete = async (productId) => {
        if (window.confirm('   ?')) {
            try {
                //   
                const deleteResponse = await fetch(BASE_URL+`/api/admin/products/${productId}`, {
                    method: 'DELETE',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json'
                    }
                });

                if (!deleteResponse.ok) {
                    throw new Error('    .');
                }

                toast.success(' .', {
                    position: "top-center",
                    autoClose: 2000,
                });

                //       
                const productsResponse = await fetch(BASE_URL+'/api/products/all', {
                    method: 'GET',
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json'
                    }
                });

                if (!productsResponse.ok) {
                    throw new Error('     .');
                }

                const updatedProducts = await productsResponse.json();
                setProducts(updatedProducts); //     

            } catch (error) {
                console.error('    :', error);
                toast.error('    .', {
                    position: "top-center",
                    autoClose: 2000,
                });
            }
        }
    };

    //  ID     
    const getCategoryNameById = (categoryId) => {
        const category = categories.find(cat => cat.id === categoryId);
        return category ? category.name : ' '; //     
    };

    return (
        <div className="product-list card border-0">
            <h2 className="mb-4"> </h2>
            {products.length > 0 ? (
                <div>
                    <table className="table table-striped">
                        <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th> </th>
                            <th></th>
                        </tr>
                        </thead>
                        <tbody>
                        {products.map(product => (
                            <tr key={product.id}>
                                <td>{product.name || 'N/A'}</td>
                                <td>{product.description || ' '}</td>
                                <td>{getCategoryNameById(product.categoryId)}</td>
                                <td>
                                    <button
                                        className="btn-dark-gray me-2"
                                        onClick={() => navigate(`/admin/edit-product/${product.id}`)}
                                    >
                                        
                                    </button>
                                    <button
                                        className="btn-dark-gray"
                                        onClick={() => handleDelete(product.id)}
                                    >
                                        
                                    </button>
                                </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>

                    <button
                        className="btn-black mt-3"
                        onClick={() => navigate('/admin/createproduct')}
                    >
                         
                    </button>
                </div>
            ) : (
                <p className="text-muted"> .</p>
            )}
        </div>
    );
}

export default ProductList;

