import React, { useState } from 'react';
import {Link, useNavigate} from 'react-router-dom';
import logo from "../../logo.svg";
import './FindAccount.css';

function FindAccount() {
    const [name, setName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const navigate = useNavigate();

    const handleFindAccount = async (event) => {
        event.preventDefault();

        //    
        if (!name || !phoneNumber) {
            setErrorMessage('  .');
            return;
        }

        try {
            const response = await fetch(`http://43.203.127.251:8080/api/auth/find?name=${name}&phoneNumber=${phoneNumber}`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                }
            });


            if (response.ok) {
                const data = await response.json(); //   
                const email = data.email;
                const name = data.name;
                navigate('/found', { state: { email, name } }); //    
            } else if (response.status === 404) {
                setErrorMessage('   .');
            } else {
                setErrorMessage('  .  .');
            }
        } catch (error) {
            setErrorMessage('  .    .');
        }
    };

    const handleBack = () => {
        navigate(-1); //   
    };

    return (
        <div className="user-find-container h-100 d-flex justify-content-center align-items-center card border-0">
            <div className="col-md-6">
                <div className="user-login-logo text-center mb-5">
                    <Link to="/"><img src={logo} alt="logo" width="140"/></Link>
                </div>
                <div className="d-flex justify-content-between align-items-center">
                    <h3 className="text-left mb-2"> </h3>
                    <div className="user-link">
                        <a href="#" onClick={handleBack} className="text-muted"></a>
                    </div>
                </div>
                <hr/>
                <form onSubmit={handleFindAccount}>
                    <div className="form-group mb-3">
                        <label htmlFor="name"></label>
                        <input
                            type="text"
                            className="form-control user-form-control"
                            id="name"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            required
                        />
                    </div>
                    <div className="form-group mb-3">
                        <label htmlFor="phoneNumber"></label>
                        <input
                            type="tel"
                            className="form-control user-form-control"
                            id="phoneNumber"
                            value={phoneNumber}
                            onChange={(e) => setPhoneNumber(e.target.value)}
                            required
                            style={{marginBottom: '40px'}}
                        />
                    </div>
                    {errorMessage && <p className="text-danger">{errorMessage}</p>}
                    <button type="submit" className="btn btn-sm user-btn btn-dark"> </button>
                </form>
            </div>
        </div>
    );
}

export default FindAccount;
