import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axiosInstance from '../../../utils/axiosInstance'; // Axios  
import './ProductAdmin.css';
import {toast} from "react-toastify";

function ProductOptionEdit() {
    const { detailId } = useParams(); // URL detailId( ID) 
    const navigate = useNavigate();
    const [option, setOption] = useState({
        name: '',
        price: 0,
        quantity: 0,
        mainImage: ''
    });
    const BASE_URL = "http://43.203.127.251:8080";
    const token = localStorage.getItem('access'); //  JWT  

    //   
    useEffect(() => {
        const fetchOption = async () => {
            try {
                const response = await fetch(BASE_URL+`/api/products/details/${detailId}`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Authorization': `Bearer ${token}`, // Authorization  
                    },
                });

                if (!response.ok) {
                    throw new Error('Error fetching option');
                }

                const data = await response.json();
                setOption(data); //    
            } catch (error) {
                console.error('Error fetching option:', error);
            }
        };

        fetchOption();
    }, [detailId]);

    //    
    const handleUpdate = async () => {

        try {
            const response = await fetch(BASE_URL+`/api/admin/products/details/${detailId}`, {
                method: 'PUT',
                headers: {
                    'Authorization': `Bearer ${token}`, // Authorization  
                    'Content-Type': 'application/json', // JSON   
                    'Accept': 'application/json',
                },
                body: JSON.stringify(option), // option  JSON   
            });

            if (response.ok) {
                navigate(-1); //   
            } else {
                console.error('Error updating option', response.status);
                toast.error('    .', {
                    position: "top-center",
                    autoClose: 2000,
                });
            }
        } catch (error) {
            console.error('Error updating option:', error);
            toast.error('    .', {
                position: "top-center",
                autoClose: 2000,
            });
        }
    };

    return (
        <div className="container option-edit-container my-4">
            <h1 className="mb-4"> </h1>
            <div className="form-group">
                <label> </label>
                <input
                    type="text"
                    className="form-control"
                    value={option.name}
                    onChange={(e) => setOption({ ...option, name: e.target.value })}
                />
            </div>
            <div className="form-group">
                <label></label>
                <input
                    type="number"
                    className="form-control"
                    value={option.price}
                    onChange={(e) => setOption({ ...option, price: parseFloat(e.target.value) })}
                />
            </div>
            <div className="form-group">
                <label></label>
                <input
                    type="number"
                    className="form-control"
                    value={option.quantity}
                    onChange={(e) => setOption({ ...option, quantity: parseInt(e.target.value, 10) })}
                />
            </div>
            <div className="form-group">
                <label> </label>
                <input
                    type="text"
                    className="form-control"
                    value={option.mainImage}
                    onChange={(e) => setOption({ ...option, mainImage: e.target.value })}
                />
            </div>
            <div className="text-end"> {/*       */}
                <button className="btn btn-secondary mt-3" onClick={handleUpdate}> </button> {/*   */}
            </div>
        </div>
    );
}

export default ProductOptionEdit;

